import { assestsUserAdmissionUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../PlateformUser/PlateformUser.module.css";
const NotCollectDocumentCard = ({ docs, selectList, onMultiSelect }) => {
  const onSelectMultipleStaff = () => {
    onMultiSelect(docs);
  };
  return (
    <>
      <div
        className={style.assign_show_list_each}
        onClick={onSelectMultipleStaff}
        style={{
          width: "46%",
          paddingInline: "0",
        }}
      >
        {selectList?.includes(docs?._id) ? (
          <img
            src={`${assestsUserAdmissionUrl}/check.svg`}
            alt="icon"
            style={{
              height: "1.3rem",
              width: "1.3rem",
              borderRadius: "0",
              cursor: "pointer",
            }}
          />
        ) : (
          <img
            style={{
              height: "1.3rem",
              width: "1.3rem",
              borderRadius: "0",
              cursor: "pointer",
            }}
            src={`${assestsUserAdmissionUrl}/uncheck.svg`}
            alt="icon"
          />
        )}
        <div className={style.assign_each_paragraph}>
          <h6>{docs?.document_name ?? ""}</h6>
        </div>
      </div>
    </>
  );
};

export default NotCollectDocumentCard;
