import { useAdmissionDocumentList } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import NotCollectDocumentCard from "./NotCollectDocumentCard";

const NotCollectDocument = ({ aid, onMultiSelect, selectList }) => {
  const { admissionDocumentList, admissionDocumentListLoading } =
    useAdmissionDocumentList({
      data: {
        aid: aid,
        page: 1,
        limit: 1000,
        search: "",
      },
      skip: !aid,
    });
  return (
    <>
      <div
        style={{
          display: "flex",
          width: "100%",
          flexWrap: "wrap",
          gap: "8%",
        }}
      >
        {admissionDocumentList?.list?.map((docs) => (
          <NotCollectDocumentCard
            key={docs?._id}
            docs={docs}
            selectList={selectList}
            onMultiSelect={onMultiSelect}
          />
        ))}
      </div>

      {admissionDocumentListLoading ? (
        <Loading />
      ) : (
        admissionDocumentList?.list?.length === 0 && (
          <EmptyMessage title="No document." />
        )
      )}
    </>
  );
};

export default NotCollectDocument;
