import { useTranslation } from "react-i18next";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/Income/Income.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { useState } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useAdmissionOngoingApplicationDocumentCollect } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useLocation } from "react-router-dom";
import NotCollectDocument from "./NotCollectDocument";
const DocumentCollectDocs = ({
  onClose,
  applicant,
  appId,
  onRefetch,
  admissionDocsCollect,
  aid,
}) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [state, setState] = useState({
    listId: [],
    lsit: [],
  });
  const [disabled, setDisabled] = useState(false);

  const [admissionOngoingApplicationDocumentCollect] =
    useAdmissionOngoingApplicationDocumentCollect();

  const onCollectDocument = () => {
    if (
      applicant?.student?._id &&
      appId &&
      applicant?._id &&
      applicant?.revert_request_status
    ) {
      setDisabled((pre) => !pre);
      let dt_arr = [];
      if (admissionDocsCollect) {
        dt_arr = state.lsit;
      }
      admissionOngoingApplicationDocumentCollect({
        sid: applicant?.student?._id,
        appId: appId,
        document: {
          nest: applicant?._id,
          revert_status: applicant?.revert_request_status,
          staffId: getQuery.state?.sid,
          collect_docs: dt_arr,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    }
  };

  const onMultiSelect = (assing) => {
    if (state.listId?.includes(assing?._id)) {
      setState((prev) => ({
        ...prev,
        listId: prev.listId?.filter((dt) => dt !== assing?._id),
        lsit: prev.lsit?.filter((dt) => dt?.docs !== assing?._id),
      }));
    } else {
      setState((prev) => ({
        ...prev,
        lsit: [
          ...prev.lsit,
          {
            docs: assing?._id,
            not_filled: "Yes",
          },
        ],
        listId: [...prev.listId, assing?._id],
      }));
    }
  };
  console.info("applicant", state);

  return (
    <>
      <Modal onClose={onClose}>
        <div
          className={style.modal_container}
          style={{
            minWidth: "48rem",
            maxWidth: "48rem",
          }}
        >
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("collect_required_document")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            {admissionDocsCollect ? (
              <UserMemberContentWrapper
                customStyle={{
                  paddingTop: "0.3rem",
                  marginBottom: "1rem",
                }}
              >
                <div
                  className={style.modal_container_header}
                  style={{
                    marginBottom: "0.5rem",
                  }}
                >
                  <h6>{t("required_document_not_submitted")}</h6>
                </div>
                <NotCollectDocument
                  aid={aid}
                  selectList={state.listId}
                  onMultiSelect={onMultiSelect}
                />
              </UserMemberContentWrapper>
            ) : null}
            <div
              className={style.modal_container_header}
              style={{
                flexDirection: "column",
              }}
            >
              <p>{t("collecting_documents_of_student")}</p>
              <h6>
                {`${applicant?.student?.studentFirstName ?? ""} ${
                  applicant?.student?.studentMiddleName
                    ? applicant?.student?.studentMiddleName
                    : ""
                } ${applicant?.student?.studentLastName ?? ""}`}
              </h6>
            </div>
            <CreateButton label="confirm" onAction={onCollectDocument} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default DocumentCollectDocs;
